<script>

export default {
  name: 'Login',
   data() {
        return {
          adminsForm: {
            phone: '',
            password: '',
            chake: ''
          },
          rules: {
            pass: [
            ],
            checkPass: [
            ],
            age: [
            ]
          }
        };
      },
      methods: {
        resetForm(formName) {
          this.$refs[formName].resetFields();
        },
		login(){
			const that = this
			if(this.adminsForm.password === ''){
				this.$message.error("请输入密码")
			}else{
				that.axios.post('https://106.55.164.46/admins/login',null,{
					params:{
						// params就是query参
						phone:that.adminsForm.phone,
						password:that.adminsForm.password
					}
				})
				  .then(function (res) {
					  console.log(res)
					  if(res.data.err_code == 0){
					  that.$message({
							message: '登陆成功',
							type: 'success'
						  });
					  }else{
						  that.$message.error("用户名或密码错误")
					  }
				  })
				  .catch(function (error) {
					console.log(error);
					alert("登陆失败！")
				  });
			}
		}
      }
}
</script>

